hr {
  border: 1px dashed #c8c8c8;
}

.page-id-178 {
  .adventures-page {
    display: grid;
    grid-template-columns: reapea(2, 1fr);
    grid-template-rows: repeat(2, 500px);
    grid-gap: 1.5rem;
    margin-left: auto;
    margin-right: auto;
  }
 
  
  h3 {
    text-transform: capitalize;
    padding-left: 20px;
    color: white;
  }

 
  .adventures-1 {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../.././assets/images/adventure-photos/canoe-girl.jpg);
    background-position: center;
    background-size: cover;
    grid-column: 1;
    grid-row: 1;
    border: 4px solid white;
    margin-left: 0;
  }

  .adventures-2 {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../.././assets/images/adventure-photos/beach-bonfire.jpg);
    background-position: center;
    background-size: cover;
    grid-column: 2;
    grid-row: 1;
    border: 4px solid white;

  }

  .adventures-3 {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../.././assets/images/adventure-photos/mountain-hikers.jpg);
    background-position: center;
    background-size: cover;
    grid-column: 1;
    grid-row: 2;
    border: 4px solid white;
  }

  .adventures-4 {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../.././assets/images/adventure-photos/night-sky.jpg);
    background-position: center;
    background-size: cover;
    grid-column: 2;
    grid-row: 2;
    border: 4px solid white;
  }
  p.read-more {
      margin-left: 20px;
      width: 130px;
      background: none;
      text-transform: uppercase;
      color: $color__nav-text;
      border: 1px solid white;
    padding:0.75rem;
  }

}



